
.btn{
  padding: 50px;
  align-items: center;
  display: flex;
  margin-top: 50px;
  font-size: 16px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  color: navy;
  width: 300px;
  height: 300px;
  border-radius: 30px;
  -webkit-box-shadow: 4px 7px 12px -5px rgba(0,0,0,0.92); 
  box-shadow: 4px 7px 12px -5px rgba(0,0,0,0.92);
  
}
 .btn:hover {
   background-color: rgb(221, 221, 86);
   width: 350px;
  
  
 }

